.about {
  position: relative;
  padding: 80px 0;
  overflow: hidden;
  background:transparent;
}

.about-content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
  opacity: 0.3;
}

.about-text-container {
  background-color: rgba(20, 33, 61, 0.9);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 15px 30px rgba(255, 61, 0, 0.3);
  max-width: 700px;
  margin: 20px auto;
  transform: perspective(1000px) rotateX(5deg) translateY(50px);
  opacity: 0;
  transition: transform 1s ease, opacity 1s ease;
}

.about-text-container.visible {
  opacity: 1;
  transform: perspective(1000px) rotateX(5deg) translateY(0);
}

.about-text-container:hover {
  transform: perspective(1000px) rotateX(0deg) translateY(0);
}

.about h2 {
  font-size: 3rem;
  color: var(--primary-accent);
  margin-bottom: 30px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.about p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: var(--text-light);
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .about-text-container {
    padding: 30px;
    margin: 10px;
    transform: translateY(50px);
    transition: transform 0.8s ease, opacity 0.8s ease;
  }

  .about-text-container.visible {
    transform: translateY(0);
  }

  .about-text-container:hover {
    transform: translateY(0);
  }

  .about h2 {
    font-size: 2.5rem;
  }

  .about p {
    font-size: 1.1rem;
  }
}